/* stylelint-disable prettier/prettier */
/* stylelint-enable prettier/prettier */
.overlay-container {
  overflow: hidden !important;
}
.overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  scrollbar-width: none;
  /*
     * Псевдо-элементы занимают пустое месте над и под контентом,
     * выравнивая его по центру вертикали
     * Такой способ выравнивания работает лучше, чем justify-content: center,
     * потому что корректно позиционирует контент, превышающий экран по высоте
     */
}
.overlay::-webkit-scrollbar {
  width: 0;
}
.overlay::before,
.overlay::after {
  content: '';
  flex-grow: 1;
}
.overlay__content {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.overlay__close {
  display: block;
  position: fixed;
  top: 10px;
  right: 10px;
  width: 24px;
  height: 24px;
  color: #ffffff;
  z-index: 501;
}
.overlay__close svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
}

