/* stylelint-disable prettier/prettier */
/* stylelint-enable prettier/prettier */
/* Устанавливает размер шрифта и высоту строки по умолчанию */
/* Применяет высоту строки, соответствующую высоте шрифта */
/* Для исключительных комбинаций размера шрифта и высоты строки */
/* prettier-ignore */
/* stylelint-disable custom-media-pattern */
/* Базовая вёрстка модального окна.
 * Для отображения поверх других элементов
 * модальное окно должно быть обёрнуто в .overlay
 */
.modwin {
  --modwin-margin: 15px;
  --modwin-v-padding: 25px;
  --modwin-h-padding: 45px;
  --modwin-border-radius: 3px;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 500px;
  min-width: 360px;
  box-sizing: border-box;
  margin: var(--modwin-margin);
  padding: 25px 45px;
  padding: var(--modwin-v-padding) var(--modwin-h-padding);
  border-radius: var(--modwin-border-radius);
  background-color: #ffffff;
}
@media (max-width: 480px) {
  .modwin {
    --modwin-margin: 0;
    --modwin-border-radius: 0;
    width: 100% !important;
    height: 100% !important;
    max-width: auto;
    overflow: auto;
    --modwin-h-padding: 15px;
  }
}
.modwin .pro-loader {
  border-radius: var(--modwin-border-radius);
}
/* Модал увеличенной ширины */
.modwin--wide {
  width: 100%;
  max-width: 700px;
}
/* Модал ещё более увеличенной ширины
   (что-то много развелось модалов, надо с Русланом переговорить).
*/
.modwin--wider {
  width: 100%;
  max-width: 800px;
}
/* Модальное окно, занимающее всю площадь экрана */
.modwin--fullscreen {
  --modwin-margin: 0;
  --modwin-border-radius: 0;
  width: 100% !important;
  height: 100% !important;
  max-width: auto;
  overflow: auto;
}
/* Модальное окно без отступов. */
.modwin--no-pad {
  --modwin-v-padding: 0;
  --modwin-h-padding: 0;
}
/* Обёртка для содержимого модала (главным образом нужна для правильного определения first- и last-child */
.modwin__content {
  flex-grow: 1;
}
.modwin__content > :first-child {
  border-top-left-radius: var(--modwin-border-radius);
  border-top-right-radius: var(--modwin-border-radius);
}
.modwin__content > :last-child {
  border-bottom-left-radius: var(--modwin-border-radius);
  border-bottom-right-radius: var(--modwin-border-radius);
}
/* Заголовок модального окна */
.modwin__caption {
  font-size: 1.53846154rem;
  line-height: 1.4;
  font-family: inherit;
  font-weight: 900;
  width: 100%;
  margin: 5px 0 20px;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: center;
  color: #2b2b2b;
}
/* Уменьшенный заголовок модального окна */
.modwin__mid-caption {
  font-size: 1.23076923rem;
  line-height: 1.5;
  font-family: inherit;
  font-weight: 900;
  width: 100%;
  margin: 15px 0 10px;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: center;
  color: #2b2b2b;
}
/* Подзаголовок модального окна */
.modwin__sub-caption {
  font-size: 1.07692308rem;
  line-height: 1.57142857;
  width: 100%;
  margin: 0 0 20px;
  padding: 0 10px;
  box-sizing: border-box;
  text-align: center;
}
.modwin__caption + .modwin__sub-caption {
  margin-top: -15px;
}
.modwin__sub-caption--small {
  font-size: 1rem;
  line-height: 1.53846154;
  margin-top: -5px;
}
/* Элементы с выносом на поля модала вверх/вправо/вниз/влево */
.modwin__y-pull-top {
  margin-top: calc(-1 * var(--modwin-v-padding));
}
.modwin__x-pull-right {
  margin-right: calc(-1 * var(--modwin-h-padding));
}
.modwin__y-pull-bottom {
  margin-bottom: calc(-1 * var(--modwin-v-padding));
}
.modwin__x-pull-left {
  margin-left: calc(-1 * var(--modwin-h-padding));
}
/* Элементы с выносом на поля модала с обеих сторон */
.modwin__x-pull {
  margin-left: calc(-1 * var(--modwin-h-padding));
  margin-right: calc(-1 * var(--modwin-h-padding));
}
.modwin__y-pull {
  margin-top: calc(-1 * var(--modwin-v-padding));
  margin-bottom: calc(-1 * var(--modwin-v-padding));
}
/* Кнопка закрытия модала */
.modwin__close {
  display: block;
  height: 1.84615385rem;
  width: 1.84615385rem;
  position: absolute;
  top: 1.15384615rem;
  right: 1.15384615rem;
  cursor: pointer;
  color: #c8c8c8;
}
.modwin__close:hover,
.modwin__close:focus {
  color: #7b7b7b;
}
.modwin__close svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
}
/* Горизонтальный разделитель блоков в модале */
.modwin__hr {
  margin-left: calc(-1 * var(--modwin-h-padding));
  margin-right: calc(-1 * var(--modwin-h-padding));
  flex: 1 0 100%;
  height: 1px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.modwin__hr::after {
  content: '';
  display: block;
  width: 100%;
  border-top: 1px solid #ededed;
}
/* Блок с роботом и фоном на всю ширину модала */
.modwin__alert {
  font-size: 1rem;
  line-height: 1.53846154;
  margin-left: calc(-1 * var(--modwin-h-padding));
  margin-right: calc(-1 * var(--modwin-h-padding));
  position: relative;
  margin-top: 25px;
  background-color: #eef4f7;
}
.modwin__alert:first-child {
  margin-top: calc(-1 * var(--modwin-v-padding));
}
.modwin__alert:last-child {
  margin-bottom: calc(-1 * var(--modwin-v-padding));
}
@media (min-width: 480.001px) {
